.cover {
  background: transparent url(coverimg.jpg) no-repeat center center;
  background-size: cover;
  padding: 2rem 1rem;
  height: 18rem;
}

.salary-info {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.med-sal {
  font-size: 1.2em;
  color: white;
  padding: 0;
  margin: 0;
}

.median {
  color: white;
  font-size: 4.5em;
  margin: 0;
  padding: 0;
}

.tri-color-band {
  height: 0.625rem;
  display: flex;
  justify-content: center;
  width: 100%;
}

.yellow {
  background: #f9ae06;
  flex: 1;
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: 0%;
}

.red {
  background: #ef3852;
  flex: 1;
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: 0%;
}

.blue {
  background: #12f1fc;
  flex: 1;
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: 0%;
}

.cover-title {
  color: #ffffff;
  font-size: 3em;
  width: 50%;
}

@media screen and (max-width: 1250px) {
  .cover-title {
    font-size: 2.7em;
  }
}

@media screen and (max-width: 1060px) {
  .med-sal {
    font-size: 1em;
  }

  .median {
    font-size: 3em;
  }
}

@media screen and (max-width: 875px) {
  .salary-info {
    align-items: flex-start;
    margin-top: 10%;
  }

  .median {
    font-size: 2.4em;
  }

  .cover-title {
    font-size: 2em;
  }
}

@media screen and (max-width: 750px) {
  .median {
    font-size: 3em;
  }

  .cover-title {
    font-size: 1.8em;
  }
}

@media screen and (max-width: 450px) {
  .med-sal {
    display: none;
  }

  .median {
    display: none;
  }

  .cover-title {
    font-size: 2em;
  }
}
